import React from 'react'
import { useStaticQuery, graphql } from 'gatsby'

import Container from 'components/ui/Container'
import Button from 'components/ui/Button'
import TitleSection from 'components/ui/TitleSection'

import * as Styled from './styles'

const Newsletter = () => {
  const { markdownRemark } = useStaticQuery(graphql`
    query {
      markdownRemark(frontmatter: { category: { eq: "deputy contact" } }) {
        frontmatter {
          title
          subtitle
          namePlaceholder
          emailPlaceholder
          submitPlaceholder
        }
      }
    }
  `)

  const deputyContact = markdownRemark.frontmatter

  return (
    <Styled.Newsletter>
      <Container section>
        <TitleSection
          title={deputyContact.title}
          subtitle={deputyContact.subtitle}
          center
        />
        <Styled.Form>
          <Styled.Input type="text" placeholder={deputyContact.namePlaceholder} />
          <Button primary block>
            {deputyContact.submitPlaceholder}
          </Button>
        </Styled.Form>
      </Container>
    </Styled.Newsletter>
  )
}

export default Newsletter
